@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
}


.create-task__container {
  margin: auto;
  width: 40%;
  margin-top: 75px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 12px;
  margin-bottom: 4vh;
}

.create-task__interface {
  display: flex;
  flex-direction: column;
}

.create-task__interface p {
  font-weight: 600;
  margin: 2vh 0 0 2vh;
}

.create-task__interface input {
  padding: 0.7rem;
  margin: 1vh 2vh 2vh 2vh;
}

.create-task__interface button {
  font:inherit;
  width: 20%;
  margin: 0 2vh 2vh 2vh;
  height: 2.5rem;
  background: #c02e4ef6;
  border: 1px solid #c02e4ef6;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

.create-task__interface button:hover {
  background: #ff014d;
  border-color: #ff014d;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
}
.short-text{
  display:none;
}

@media (max-width: 900px) {
  .create-task__container{
    max-width: 70%;
  }
  .create-task__interface {
    align-items: center;
    margin:auto;
  }
  .create-task__interface p {
    margin-right: 3vw;
  }
  .create-task__interface button {
    width: 40%;
    height: 2rem;
  }
  .create-task__interface input {
    width: 90%;
    padding: 0.5rem;
  }
  .short-text {
    display:none;
  }
}

@media (max-width: 450px) {
  .create-task__interface button {
    font-size: .8em;
  }
}

@media (max-width:350px) {
    .long-text {
      display: none;
    }
    .short-text {
      display: inline-block
    }
}
.task-item {
  width: 80%;
  padding: 2vh;
  margin: auto;
  margin-top: 1vh;
  margin-bottom:1vh;
  background: #c02e4ef6;
  border-radius: 7px;
  box-shadow: rgba(88, 74, 74, 0.24) 0px 3px 8px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.task-details {
  display: flex;
  align-items: center;
}

.task-item label {
  font-size: 1.5em;
  margin-left: 1vw;
  margin-bottom: 3.5px;
  cursor: pointer;
}

.task-item input[type="checkbox"] {
  -webkit-appearance: none;
  width: 28px;
  height: 28px;
  background: white;
  border-radius: 4px;
  border: 2px solid black;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.task-item input[type="checkbox"]:checked,
.task-item input[type="checkbox"]:checked:hover {
  background: #5bcd3e;
}

.task-item input[type="checkbox"]:hover {
  background: rgb(170, 166, 166);
}

.task-item input[type="checkbox"]:checked::after {
  font-family: "Font Awesome 5 Free";
  content: "\f00c";
  font-weight: 900;
  font-size: 18px;
  color: white;
}

.delete-task {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.delete-task::after {
  font-family: "Font Awesome 5 Free";
  content: "\f00d";
  font-weight: 900;
  font-size: 23px;
  color: rgb(165, 45, 45);
}

.delete-task:hover {
  background: #b9b2ae;
}

.opacity {
  opacity: 0.5;
}

.opacity label {
  text-decoration: line-through;
}

@media (max-width: 770px) {
    .task-item label {
        margin-left: 1.2vw;
        margin-bottom: 2px;
    }
    
    .task-item { 
        width: 100%;
        max-height:inherit;
    }

    .task-item input[type="checkbox"] {
        min-width: 20px;
        min-height:20px;
        max-width: 22px;
        max-height:22px;
    }

    .task-item input[type="checkbox"]:checked::after {
        font-size: 13px;
    }
    
    .delete-task {
        min-width: 22px;
        min-height: 22px;
    }
    .delete-task::after {
        font-size: 20px;
    }
}

@media (max-width:500px){
    .task-item label {
        font-size: 1.2em;
    }
}

@media (max-width:350px) {
    .task-item label {
        font-size: 1em;
    }
}

.task-container {
    display: flex;
    flex-direction: column;
    width: 75%;
    margin: auto;
}
